.rock {
  background-color: rgb(148, 81, 81);
}
.ghost {
  background-color: rgb(247, 247, 247);
}
.electric {
  background-color: rgb(255, 255, 161);
}
.bug {
  background-color: #F6D6A7;
}
.poison {
  background-color: #e0a7f6;
}
.normal {
  background-color: #F4F4F4;
}
.fairy {
  background-color: rgba(255, 192, 203, 0.863);
}
.fire {
  background-color: #FBE3DF;
}
.grass {
  background-color:#E2F9E1;
}
.water {
  background-color:#E0F1FD;
}

a {
  text-decoration: none;
  color: black;
}

.position {
  position: fixed;
  right: 5%;
  bottom: 200px;
}

.app-contaner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 3rem 0.5rem;
}
.pokemon-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}
.all-container {
  max-width: 600px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.thumb-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0 ;
  margin: 0.3rem;
  border: 1px solid #efefef;
  border-radius: 0.2rem;
  min-width: 160px;
  text-align: center;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.089);
  text-decoration: none;
}

h3 {
  margin-bottom: 0.2rem;
}

.characteristics_table {
  display: flex;
  width: 100%;
  justify-content: space-between;
  font-size: 11px;
}

.characteristics   {

  width: 70%;
  border: 1px solid black;
  box-sizing: border-box;
  padding: 4px;
}
.value {
  width: 30%;
  border: 1px solid black;
  box-sizing: border-box;
  padding: 4px;
}

.type_button {
  max-width: max-content;
  padding: 4px 14px;
  margin: 5px 10px;
  border-radius: 3px;
}

h3::first-letter {
  text-transform:capitalize;
}

.thumb-container .number {
  border-radius: 1rem;
  padding: 0.2rem 0.4rem;
  background-color: rgba(255,255,255,0.3);
}
.thumb-container img {
  width: 120px;
  height: 120px;
}

.detail-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.detail-wrapper button {
  color: rgb(22, 22, 22);
  padding: .5rem;
  margin-top: 1rem;
  border: none;
  border-radius: 0.2rem;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.185);
}
.load-more {
    background: dodgerblue;
    border-radius: 10px;
    border: none;
    color: white;
    font-weight: 600;
    padding: 0.5rem 1.5rem;
    min-width: 50%;
    margin-top: 1rem;
    cursor: pointer;
}
.load-more:hover {
  background-color: cadetblue;
  box-shadow: 0px 0px 5px cadetblue;
}

@media (max-width: 1000px) {
  .all-container {
    width: 350px;
  }
  .pokemon-container {
    margin-right: auto;
  }
}

@media (max-width: 650px) {
  .all-container {
    width: 170px;
  }
  .pokemon-container {
    width: 170px;
  }
  .position {
    right: 0%;
  }
}